import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FacilityReportsComponent } from './facility-reports/facility-reports.component';
import { FacilityReportDataComponent } from './facility-reports/facility-report-data/facility-report-data.component';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

const routes: Routes = [
  { path: '', component: FacilityReportsComponent, canActivate: [AuthGuard] },
	{ path: ':name/:id', component: FacilityReportDataComponent, canActivate: [AuthGuard]},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityReportRoutingModule { }
